html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 100rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.bg-amarillo {
  background-color: #fef46d;
}

.bg-verde {
  background-color: #4cb8b3;
}

.header {
  background-color: #4cb8b3;
}
.header .contenido-header {
  padding: 2.5rem 0;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  color: #fff;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0 0.3rem 0.3rem #444;
}

.navegacion-principal {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.navegacion-principal a {
  color: #fff;
  font-size: 2rem;
}
.navegacion-principal a:hover {
  color: #fef46d;
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
  /***************************************/
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#80ffffff",GradientType=1 );
}
.video .contenido-video {
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
}
.video .contenido-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #fff;
  font-size: 5rem;
}
.video .contenido-video h2 span {
  background-color: #f53756;
  font-size: 1.8rem;
  padding: 1rem 2rem;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenido-festival h2 {
  font-size: 3rem;
}
.contenido-festival .fecha {
  font-weight: 700;
  color: #f53756;
}
.contenido-festival .texto {
  line-height: 1.5;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #fff;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
  }
}
.escenarios-contenedor .nombre-escenario {
  padding-top: 2rem;
  color: #000;
  font-size: 2rem;
}
.escenarios-contenedor .nombre-escenario:after {
  content: "";
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.rock .nombre-escenario::after {
  background-image: url(../img/icono_rock.png);
}

.edm .nombre-escenario::after {
  background-image: url(../img/icono_edm.png);
}

.calendario {
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-verde .calendario li:nth-child(2n+1) {
  background-color: #3b9691;
}

.bg-amarillo .calendario li:nth-child(2n+1) {
  background-color: #fef03a;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  padding: 0;
  list-style: none;
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 5rem;
}
.galeria-imagenes li {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 2.5rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 2.5rem;
  }
}

.imagengrande {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-cerrar {
  color: #000;
  font-size: 3rem;
  background-color: #fff;
  width: 5rem;
  height: 5rem;
  display: grid;
  place-items: center;
  font-weight: 900;
  border-radius: 50%;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #fff;
}
.boletos .contenedor-boletos {
  margin-top: 2.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 5rem;
}
.boletos p {
  text-align: center;
  color: #fff;
  margin-bottom: 2rem;
  font-size: 3rem;
}

.pase {
  width: 100%;
  border-spacing: 0;
}
.pase thead {
  background-color: #f53756;
}
.pase thead th {
  padding: 2.5rem;
  color: #fff;
  font-weight: 300;
  font-size: 2rem;
}
.pase tbody {
  background-color: #fff;
}
.pase tbody td {
  padding: 1.25rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase tbody tr:last-of-type td {
  border: none;
}
.pase tfoot {
  background-color: #752f97;
}
.pase tfoot td {
  padding: 1.25rem;
  text-align: center;
  font-size: 3rem;
  color: #fff;
}

.footr {
  background-color: #f53756;
  padding: 1.25rem;
  text-align: center;
}
.footr p {
  color: #fff;
  margin: 0;
}